
import axios from 'axios';
import Xheader from '@/components/comother/rheader';
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ViewBox from 'vux/src/components/view-box/index.vue'

export default {
directives: {
TransferDom
},
components:{
'r-header':Xheader,
Previewer,
ViewBox
},
methods:{
show (index) {
this.$refs.previewer.show(index)
},
pass() {
axios.get('pstudy/pass', {
params: {
userid: this.$store.getters.user.userid,
studyid:this.$route.params.studyid
}
})
.then(res => {
if(res.success){
this.$vux.alert.show({title: res.msg});
userName();
}else{
this.$vux.alert.show({title:res.msg});
}
})
.catch(err => {
console.log(err);
});
},
spread(){
this.spr = true;
this.btnAn = !this.btnAn;
this.btnPack = !this.btnPack;
},
folding(){
this.spr = !this.spr;
this.btnAn = !this.btnAn;
this.btnPack = !this.btnPack;
},
noSpread(){
this.noSpr = true;
this.nobtnAn = !this.nobtnAn;
this.nobtnPack = !this.nobtnPack;
},
noFolding(){
this.noSpr = !this.noSpr;
this.nobtnAn = !this.nobtnAn;
this.nobtnPack = !this.nobtnPack;
},
logIndexChange (arg) {
console.log(arg)
},
getDetail(){
this.$http.get('pstudy/queryById?studyid='+this.$route.params.studyid
).then(res =>{
this.content= res.data;
this.content.picture = [];

this.num = res.data.pictures.length;
this.content.pictures.forEach(it=>{
console.log(it);
var obj = {};
obj.msrc = 'http://www.dlbdata.cn/dangjian/picture/show?pictureId='+it.pictureId;
obj.src = 'http://www.dlbdata.cn/dangjian/picture/show?pictureId='+it.pictureId;
this.content.picture.push(obj);
});

}).catch(err =>{
console.log(err)
})
},
dataPick(s){
Date.prototype.toLocaleString = function(){
return this.getFullYear() +'年'+ (this.getMonth()+1)+'月'+this.getDay()+'日'
};
return new Date(s).toLocaleString();
}
},
mounted(){
this.getDetail();
},
data(){
return {
contents:{rights:'评分说明',title:'驿站活动详情'},
num:0,
activeData:{},
picInfo:[],
list: [],
content:{},
spr:false,
noSpr:false,
nobtnPack:false,
nobtnAn:false,
btnPack:false,
btnAn:false,
peopleNum:null,
participants:null,
Noparticipants:null,
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect();

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}

}
}
}



